        idNo: val
      };
      vm.$store.dispatch('getUserInfoFromApplicantIdNo', params).then(function (result) {
        vm.applicantItems = result;
      }).catch(function (xhr) {});
    },
    querySelections2: function querySelections2(val) {
      var vm = this;
      var params = {
        idNo: val
      };
      vm.$store.dispatch('getUserInfoFromApplicantIdNo', params).then(function (result) {
        vm.applicantItems2 = result;
      }).catch(function (xhr) {});
    },
