    onSearchItemSelected: function onSearchItemSelected(item) {
      var vm = this;
      vm.selectedSearchItem = item; // console.log('selectedSearchItem', vm.selectedSearchItem)

      if (item['applicantIdType'] === 'business') {
        vm.thongTinChuHoSo.userType = '2'; // vm.thongTinNguoiNopHoSo.sameUser = false
      } else if (item['applicantIdType'] === 'citizen') {
        vm.thongTinChuHoSo.userType = '1';
      } else {
        vm.thongTinChuHoSo.userType = '3';
