    getApplicantList: function getApplicantList() {
      var vm = this;
      var url = '/o/rest/v2/applicants';
      vm.loadingTable = true;
      return new Promise(function (resolve) {
        vm.$store.dispatch('loadInitResource').then(function (result) {
          var param = {
            headers: {},
            params: {
              start: vm.applicantPage * vm.numberPerPage - vm.numberPerPage,
              end: vm.applicantPage * vm.numberPerPage,
              type: vm.typeSearch,
              applicantName: vm.keySearch
            }
          };
          axios.get(url, param).then(function (response) {
            var items = [];

            if (response.data.hasOwnProperty('data')) {
              items = response.data.data;
            } else {}

            var dataOut = {
              data: items,
              total: response.data['total']
            };
            vm.loadingTable = false;
            resolve(dataOut);
          }).catch(function () {
            vm.loadingTable = false;
          });
        });
      });
    },
