    fullAddress: function fullAddress(address, city, district, ward) {
      var fullAddress = '';

      if (address) {
        fullAddress += address + ', ';
      }

      if (ward) {
        fullAddress += ward + ' - ';
      }

      if (district) {
        fullAddress += district + ' - ';
      }

      if (city) {
        fullAddress += city;
      }

      return fullAddress;
    }
